$border: 1px solid #E1E3DF;

container {
  width: 100%;
  position: relative;
}

a {
  text-decoration: none;
}
header {
   padding: 10px 5px 10px 25px;
  border: $border;

  .title {
    // margin-right: 500px;
  }

.title a{
  color: #F56400;
  font-family: 'Alegreya', serif;
  font-size: 42px;
  padding-left: 16px;
}

.title button {
  font-size: 14px;
  padding: 7px 15px 9px 15px ;
  border: 1px solid #40B0DC;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background-color: #34A8C4;
  color: white;
  position: absolute;
  top: 25px;
  left: 396px;
}
.search {
  color: #C1C3BF;
  font-size: 14px;
  padding-left: 10px;
  width: 255px;
  height: 30px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  border: $border;
  position: absolute;
  top: 25px;
  left: 137px;


}
.sideSelectors {
  position: absolute;
  top: 25px;
  right: 110px;
}
.sideSelectors a {
  font-size: 14px;
  margin-right: 12px;
  color: #34A8C4;
}
.sideSelectors button {
  font-size: 14px;
  background-color: white;
  color: #34A8C4;
  border: 1px solid #34A8C4;
  border-radius: 3px;
  padding: 7px 10px;
}

}
.greySelectors {
  text-align: center;
  border: $border;
  height: 44px;
  display:flex;
  justify-content: center;

  a {
    color: #888886;
    font-size: 14px;
    padding: 15px 15px 0 10px;
  }
}

  .maincontent {
    background-color: #F5F5F1;
  }
